import { surveyResponseService } from './supabaseService';

// Simple CSV export as fallback until xlsx is installed
export const exportToCSV = async (language: 'en' | 'am' = 'en') => {
  try {
    console.log(' Starting CSV export...');
    
    // Fetch all data
    const responses = await surveyResponseService.getAll();
    const summaryData = await surveyResponseService.getOverallSummary();
    
    if (!responses || responses.length === 0) {
      alert(language === 'am' ? '   ' : 'No data to export');
      return;
    }

    console.log(` Exporting ${responses.length} responses...`);

    // Create CSV content
    let csvContent = '';
    
    // Add header
    csvContent += language === 'am' ? '  \n' : 'Customer Satisfaction Report\n';
    csvContent += language === 'am' ? '       /\n' : 'Lemi Kura Sub-City Peace and Security Office\n';
    csvContent += `${language === 'am' ? ' ' : 'Report Date'}: ${new Date().toLocaleDateString()}\n\n`;
    
    // Add summary
    csvContent += language === 'am' ? ' \n' : 'Executive Summary\n';
    csvContent += `${language === 'am' ? ' ' : 'Total Responses'}: ${responses.length}\n`;
    csvContent += `${language === 'am' ? '  ' : 'Overall CSAT Score'}: ${summaryData.overallCSAT?.toFixed(2) || '0.00'}\n`;
    csvContent += `${language === 'am' ? ' ' : 'Response Rate'}: ${((summaryData.responseRate || 0) * 100).toFixed(1)}%\n\n`;
    
    // Add dimension scores
    csvContent += language === 'am' ? '  \n' : 'Service Quality Dimensions\n';
    csvContent += `${language === 'am' ? '' : 'Tangibility'}: ${summaryData.dimensionScores?.tangibility?.toFixed(2) || '0.00'}\n`;
    csvContent += `${language === 'am' ? ' ' : 'Responsiveness'}: ${summaryData.dimensionScores?.responsiveness?.toFixed(2) || '0.00'}\n`;
    csvContent += `${language === 'am' ? '' : 'Reliability'}: ${summaryData.dimensionScores?.reliability?.toFixed(2) || '0.00'}\n`;
    csvContent += `${language === 'am' ? ' ' : 'Assurance'}: ${summaryData.dimensionScores?.assurance?.toFixed(2) || '0.00'}\n`;
    csvContent += `${language === 'am' ? '' : 'Empathy'}: ${summaryData.dimensionScores?.empathy?.toFixed(2) || '0.00'}\n\n`;
    
    // Add raw data header
    csvContent += language === 'am' ? ' \n' : 'Raw Data\n';
    csvContent += [
      language === 'am' ? '' : 'Date',
      language === 'am' ? '' : 'Gender',
      language === 'am' ? '' : 'Age',
      language === 'am' ? ' ' : 'Marital Status',
      language === 'am' ? ' ' : 'Education Level',
      language === 'am' ? ' ' : 'Overall Score'
    ].join(',') + '\n';
    
    // Add response data
    responses.forEach(response => {
      const row = [
        new Date(response.created_at).toLocaleDateString(),
        language === 'am' ? (response.gender === 'male' ? '' : '') : response.gender,
        response.age,
        language === 'am' ? 
          (response.marital_status === 'married' ? '' :
           response.marital_status === 'single' ? '' :
           response.marital_status === 'divorced' ? '' : '/') :
          response.marital_status,
        response.education_level,
        response.overall_score?.toFixed(2) || '0.00'
      ];
      csvContent += row.join(',') + '\n';
    });

    // Create and download file
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    
    const timestamp = new Date().toISOString().split('T')[0];
    const filename = language === 'am' 
      ? `---${timestamp}.csv`
      : `Customer-Satisfaction-Report-${timestamp}.csv`;
    
    link.setAttribute('download', filename);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    console.log(' CSV export completed successfully');
    alert(language === 'am' 
      ? `    ${filename} `
      : `Report successfully exported to ${filename}`
    );

  } catch (error: any) {
    console.error(' Export error:', error);
    alert(language === 'am' 
      ? ` CSV   : ${error.message}`
      : `CSV export failed: ${error.message}`
    );
  }
};